// 
// LeonSu
// docIdString
// uploadSuccess
<template>
  <div id="easystarscan" style="width: 99%; height: 99%; overflow: hidden">
    <el-row style="margin: 10px 0;">
        <el-button v-if="false" size="mini" type="success" @click="beginExport" style="margin-right: 20px;"><i class="el-icon-folder-opened"></i> pdf</el-button>
        <el-button size="mini" @click="beginNew" style="background: yellow; border-color: yellow"><i class="el-icon-folder"></i> </el-button>
        <el-button size="mini" type="success" @click="beginSave"><i class="el-icon-folder-checked"></i> </el-button>
        <el-button size="mini" type="warning" @click="beginLoad"><i class="el-icon-folder-opened"></i> </el-button>
        <el-button size="mini" type="primary" @click="beginScan" style="margin-right: 20px;margin-left: 20px;"><i class="el-icon-picture"></i> </el-button>
        <el-button size="mini" type="warning" @click="beginUpload"><i class="el-icon-upload"></i> </el-button>
    </el-row>
    <el-row :gutter="24" type="flex" class="scanRow" style="width: 100%; 100%; margin: auto 0px;padding: 10px 0px;">
      <!-- <el-scrollbar style="height: 100%; width: 100%; overflow: scroll;"> -->
        <div style="width: 100%; height: 100%; overflow: hidden">
          <div id="imgTooles" ref="imgTools" style="width: 100%; overflow-y: scroll">
            <el-col v-for="(item, index) in this.imgList" :key="index" :xs="6" :sm="4" :md="3">
              <div style="background: white; width: 100px; height: 135px; display: flex; justify-content:center; align-items:Center; position: relative; border:1px solid black; margin-bottom: 10px;" @mouseenter="showDelPanel(item, true)" @mouseleave="showDelPanel(item, false)">
                <img :ref="'img' + index" :src="item.url" width="100px" class="imgStyle">
                <div ref="deletePanel" class="panelStyle" v-if="item.show">
                  <el-tooltip effect="dark" content="" placement="bottom">
                    <i class="el-icon-error" style="color: red; font-size: 22px; transform: translate(0%, 25%); cursor: pointer" @click="btnDelClick(index)"></i>
                  </el-tooltip>
                </div>
              </div>
            </el-col>
          </div>
        </div>
      <!-- </el-scrollbar> -->
    </el-row>
    <el-dialog
      :visible.sync="dialogVisible"
      width="30%"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :show-close="false"
       top="30vh"
      class="mydialog">
      <i class="el-icon-loading" v-if="isWaiting" style="font-size: 100px; color: blue"></i>
      <el-progress v-if="!isWaiting" type="circle" color="#8e71c7" :stroke-width="6" :percentage="percent" style="color: yellow"></el-progress>
    </el-dialog>
  </div>
</template>

<script>
import Viewer from 'viewerjs'
import 'viewerjs/dist/viewer.css'
export default {
  name: 'EasyStarScan',
  components: {
    viewer: Viewer
  },
  props: {
    docId: String // watch
  },
  data () {
    return {
      isWaiting: false,
      dialogVisible: false,
      percent: 0,
      showing: false,
      webSocket: null,
      lockReconnect: false,
      status: '',
      imgList: [],
      percentText: '',
      scanId: ''
    }
  },
  computed: {
    disHeight: function () {
      console.log(window.innerHeight)
      return window.innerHeight + 'px'
    },
    getStatus: function () {
      switch (status) {
        case 'state_scanning':
          return 'danger'
        default:
          return ''
      }
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.initViewer()
    })
  },
  methods: {
    init () {
      this.$refs.imgTools.style.height = window.innerHeight - 158 + 'px'
      this.imgList = []
      this.webSocketInit()
    },
    close () {
      this.webSocket.onopen = null
      this.webSocket.onmessage = null
      this.webSocket.onclose = null
      this.webSocket.onerror = null
      this.webSocket.close()
      this.webSocket = null
    },
    windowResize () {
      this.$refs.imgTools.style.height = window.innerHeight - 158 + 'px'
    },
    getCookie () {
      // cookie
      var strCookie = document.cookie
      // cookie/
      var arrCookie = strCookie.split('; ')
      // cookiecookie
      for (var i = 0; i < arrCookie.length; i++) {
        var arr = arrCookie[i].split('=')
        // userIdcookie
        if (arr[0] === 'JSESSIONID') {
          return arr[1]
        }
      }
      return ''
    },
    initViewer () {
      const ViewerDom = document.getElementById('imgTooles')
      this.viewer = new Viewer(ViewerDom, {
        'inline': false,
        'button': true, // 
        'navbar': false, // 
        'title': true, // 
        'tooltip': true, // 
        'movable': true, // 
        'zoomable': true, // 
        'rotatable': true, // 
        'scalable': true, // 
        'transition': false, //  CSS3 
        'fullscreen': false, // 
        'keyboard': true, // 
        toolbar: { // 
          zoomIn: {
            show: 1,
            size: 'large'
          },
          zoomOut: {
            show: 1,
            size: 'large'
          },
          oneToOne: {
            show: 1,
            size: 'large'
          },
          reset: {
            show: 0,
            size: 'large'
          },
          prev: {
            show: 1,
            size: 'large'
          },
          play: {
            show: 0,
            size: 'large'
          },
          next: {
            show: 1,
            size: 'large'
          },
          rotateLeft: {
            show: 1,
            size: 'large',
            click: this.rotateLeft
          },
          rotateRight: {
            show: 1,
            size: 'large',
            click: this.rotateRight
          },
          flipHorizontal: {
            show: 0,
            size: 'large'
          },
          flipVertical: {
            show: 0,
            size: 'large'
          }
        }
      })
      this.dialogVisible = false
    },
    webSocketInit () {
      this.connecting = true
      this.webSocket = new WebSocket('ws://' + this.WS_API + '/scan/tskj')
      this.webSocket.onopen = this.websocketonopen
      this.webSocket.onmessage = this.webSocketMessage
      this.webSocket.onclose = this.websocketclose
      this.webSocket.onerror = this.websocketerror
    },
    // webSocket
    webSocketMessage (evt) {
      if (this.isDebug) {
        console.log(JSON.parse(evt.data))
      }
      var json = JSON.parse(evt.data)
      switch (json.code) {
        case -4:
        case -3:
        case -2:
        case -1:
          this.$notify({
            title: '',
            message: json.errMsg,
            type: 'error'
          })
          this.dialogVisible = false
          this.percent = 0
          this.percentText = ''
          break
        case 0:
          this.scanId = json.data
          break
        case 2:
          this.viewer.destroy()
          this.addImage(json.data)
          setTimeout(this.initViewer, 1000)
          this.$notify({
            title: '',
            message: '',
            type: 'success'
          })
          this.dialogVisible = false
          this.percent = 0
          this.percentText = ''
          break
        case 3:
          if (json.data <= 100) {
            this.percent = json.data
            if (json.data === 100) {
              this.percentText = 'success'
            }
          } else {
            if (json.data > 100) {
              this.percent = 99
            } else {
              this.percent = 0
            }
          }
          break
        case 4:
          this.status = json.data
          break
        case 5:
          // console.log(this.viewer)
          var item = this.imgList[this.viewer.index]
          item.url = item.srcUrl + '?t=' + Math.random()
          // if (item.width === 100) {
          //   item.width = 133
          //   item.height = 100
          // } else {
          //   item.with = 100
          //   item.height = 133
          // }
          this.$notify({
            title: '',
            message: '',
            type: 'success'
          })
          this.dialogVisible = false
          this.percent = 0
          this.percentText = ''
          break
        case 6:
          // var item1 = this.imgList[this.viewer.index]
          this.imgList.splice(json.data, 1)
          this.$notify({
            title: '',
            message: '',
            type: 'success'
          })
          this.dialogVisible = false
          this.percent = 0
          this.percentText = ''
          break
        case 7:
          this.$emit('uploadSuccess')
          this.dialogVisible = false
          this.percent = 0
          this.percentText = ''
          this.$confirm('\n', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'success'
          }).then(() => {
            this.confiremNew()
          })
          break
        case 9:
          this.dialogVisible = false
          this.viewer.destroy()
          this.$notify({
            title: '',
            message: '',
            type: 'success'
          })
          this.imgList = []
          break
        case 10:
          this.dialogVisible = false
          this.$notify({
            title: '',
            message: '',
            type: 'success'
          })
          break
        case 11:
          this.dialogVisible = false
          this.$notify({
            title: '',
            message: '',
            type: 'success'
          })
          var jsonFiles = JSON.parse(json.data)
          for (var i = 0; i < jsonFiles.length; i++) {
            this.addImage(jsonFiles[i])
          }
          setTimeout(this.initViewer, 1000)
          break
        case 98:
          this.$notify({
            title: '',
            message: json.data,
            type: 'error'
          })
          this.dialogVisible = false
          this.percent = 0
          this.percentText = ''
          break
        default:
      }
    },
    // webSocket
    webSocketSend (data) {
      if (this.isDebug) {
        console.log(data)
      }
      this.webSocket.send(JSON.stringify(data))
    },
    // 
    reConnection () {
      if (this.lockReconnect) return
      this.lockReconnect = true
      // let that = this
      setTimeout(() => {
        this.webSocketInit()
        this.lockReconnect = false
      }, 5000)
    },
    sendBaseParam () {
      var data = {}
      data.command = 'setBaseParam'
      data.param = {}
      data.param.sessionId = this.getCookie()
      data.param.token = this.TOKEN
      data.param.host = 'http://192.168.1.108' // location.origin
      data.param.docId = this.docId
      this.webSocketSend(data)
    },
    websocketonopen () {
      if (this.isDebug) {
        console.log('WebSocket')
      }
      this.sendBaseParam()
      this.$notify({
        title: '',
        message: '',
        type: 'success'
      })
    },
    websocketclose (e) { // 
      if (this.isDebug) {
        console.log('connection closed (' + e.code + ')')
      }
      this.$notify({
        title: '',
        message: '',
        type: 'warn'
      })
      this.webSocket = null
      this.reConnection()
    },
    websocketerror (e) {
      if (this.isDebug) {
        console.log('WebSocket')
      }
      this.$notify({
        title: '',
        message: '',
        type: 'error'
      })
      this.reConnection()
    },
    addImage (imgUrl) {
      var img = {}
      img.url = 'http://' + this.WS_API + imgUrl
      img.srcUrl = img.url
      img.title = imgUrl
      img.show = false
      img.index = this.imgList.length
      this.imgList.push(img)
    },
    // 
    beginScan () {
      this.dialogVisible = true
      this.isWaiting = false
      var data = {}
      data['command'] = 'scan'
      this.webSocketSend(data)
    },
    // 
    rotateLeft () {
      if (this.isDebug) {
        console.log('')
      }
      this.beginRotate(-90)
    },
    // 
    rotateRight () {
      if (this.isDebug) {
        console.log('')
      }
      this.beginRotate(90)
    },
    beginRotate (angle) {
      this.dialogVisible = true
      this.isWaiting = true
      this.viewer.rotate(angle)
      var data = {}
      data.command = 'rotate'
      data.param = {}
      data.param.angle = angle
      data.param.fileName = this.viewer.image.alt
      this.webSocketSend(data)
    },
    showDelPanel (item, visible) {
      item.show = visible
    },
    // 
    beginNew () {
      if (this.imgList.length >= 0) {
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.confiremNew()
        })
      }
    },
    confiremNew () {
      this.dialogVisible = true
      this.isWaiting = true
      var data = {}
      data.command = 'new'
      data.param = {}
      data.param.files = []
      for (var i = 0; i < this.imgList.length; i++) {
        data.param.files.push(this.imgList[i].title)
      }
      this.webSocketSend(data)
    },
    // 
    beginSave () {
      if (this.imgList.length === 0) {
        this.$notify({
          title: '',
          message: '',
          type: 'error'
        })
        return false
      }
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.dialogVisible = true
        this.isWaiting = true
        var data = {}
        data.command = 'save'
        data.param = {}
        data.param.docId = this.docId
        data.param.files = []
        for (var i = 0; i < this.imgList.length; i++) {
          data.param.files.push(this.imgList[i].title)
        }
        this.webSocketSend(data)
      })
    },
    // 
    beginLoad () {
      if (this.imgList.length > 0) {
        this.$notify({
          title: '',
          message: '',
          type: 'error'
        })
        return false
      }
      this.dialogVisible = true
      this.isWaiting = true
      var data = {}
      data.command = 'load'
      data.param = {}
      data.param.docId = this.docId
      this.webSocketSend(data)
    },
    // 
    btnDelClick (index) {
      this.$confirm('PDF', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.dialogVisible = true
        this.isWaiting = true
        var item = this.imgList[index]
        var data = {}
        data.command = 'delete'
        data.param = {}
        data.param.fileName = item.title
        data.param.index = index
        this.webSocketSend(data)
      })
    },
    beginExport () {
      this.dialogVisible = true
      this.isWaiting = false
      var data = {}
      data.scanId = this.scanId
      data.files = []
      for (var i = 0; i < this.imgList.length; i++) {
        data.files.push(this.imgList[i].title)
      }
      this.axios.post('http://127.0.0.1:51211/ExportToPdf.do', data, {
        responseType: 'blob'
      }).then((res) => {
        this.dialogVisible = false
        if (this.isDebug) {
          console.log(res)
        }
        const blob = new Blob([res.data])
        if ('download' in document.createElement('a')) { // IE
          const elink = document.createElement('a')
          elink.download = res.headers['filename']
          elink.style.display = 'none'
          elink.href = URL.createObjectURL(blob)
          document.body.appendChild(elink)
          elink.click()
          URL.revokeObjectURL(elink.href) // URL 
          document.body.removeChild(elink)
        }
      }).catch((e) => {
        this.dialogVisible = false
        this.$notify({
          title: '',
          message: e,
          type: 'error'
        })
      })
    },
    // PDF
    beginUpload () {
      this.$confirm('PDF', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.dialogVisible = true
        this.isWaiting = true
        var data = {}
        data.command = 'upload'
        data.param = {}
        data.param.files = []
        for (var i = 0; i < this.imgList.length; i++) {
          data.param.files.push(this.imgList[i].title)
        }
        this.webSocketSend(data)
      })
    }
  }
}
</script>

<style>
#easystarscan {
  text-align: center;
}
.el-row {
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }
}
.scanRow {
  background-image: url('/static/back.png');
  border: 1px solid gray;
}
.imgStyle {
  width: 100px;
  cursor: pointer;
  max-height: 133px;
  max-width: 100px;
  vertical-align: middle;
}
.panelStyle {
  width: 100px;
  position: absolute;
  bottom: 0px;
  background: transparent;
  color: #fff;
  background-color: rgba(0,0,0,0.25); /* <!--RGB-->  */
  height: 30px;
  margin: 0 auto;
}
.mydialog > .el-dialog {
  background: transparent;
  box-shadow: 0 0 0 transparent;
}
.mydialog > .el-dialog > .el-dialog__header + .el-dialog__body > .el-progress--circle > .el-progress__text  {
  color: blue
}
</style>



